// frontend/src/components/KnowledgeGraph.js
import React, { useMemo, useState, useRef, useEffect } from 'react';
import ForceGraph2D from 'react-force-graph-2d';
import { forceManyBody, forceCollide, forceLink, forceCenter } from 'd3-force';

//  Neo4j 
const palettes = {
    neo4j: ['#ff7f0e', '#1f77b4', '#2ca02c', '#d62728', '#9467bd', '#8c564b', '#e377c2', '#7f7f7f', '#bcbd22', '#17becf'],
    vibrant: ['#ff6b6b', '#4e79a7', '#59a14f', '#f28e2b', '#b07aa1', '#9c755f', '#ff9da6', '#bab0ac', '#c7d63f', '#2ab0b8'],
    muted: ['#6b5b95','#feb236','#d64161','#ff7b25','#3a6ea5','#8fb339','#c94c4c','#7f7f7f','#b6a2d6','#b2dbbf']
};

function KnowledgeGraph({ graphData, onNodeClick }) {
    const [nodeLimit, setNodeLimit] = useState(50);
    const [showAllNodes, setShowAllNodes] = useState(false);
    const [selectedNode, setSelectedNode] = useState(null);
    const [hoveredNode, setHoveredNode] = useState(null);
    const [centerNode, setCenterNode] = useState(null);
    const [useDegreeScaling, setUseDegreeScaling] = useState(false);

    const [theme, setTheme] = useState('neo4j');

    // 
    const nodeColors = useMemo(() => {
        if (!graphData) return { 'unknown': '#7f7f7f' };

        const types = new Set();
        graphData.nodes.forEach(node => {
            if (node.type) types.add(node.type);
        });

        const colorMap = {};
        const palette = palettes[theme] || palettes.neo4j;
        Array.from(types).forEach((type, index) => {
            colorMap[type] = palette[index % palette.length];
        });
        colorMap['unknown'] = '#7f7f7f';

        return colorMap;
    }, [graphData, theme]);

    //  node.id  neo4jId  links  source/target  id
    const normalizedGraphData = useMemo(() => {
        if (!graphData) return { nodes: [], links: [] };

        //  neo4jId id
        const nodes = graphData.nodes.map(n => {
            let id = n.id;
            if (n.neo4jId !== undefined && n.neo4jId !== null) id = Number(n.neo4jId);
            else if (typeof id === 'string' && /^\d+$/.test(id)) id = Number(id);
            return { ...n, id };
        });

        // 
        const idSet = new Set(nodes.map(n => n.id));

        // 
        const pairCount = {};
        graphData.links.forEach(l => {
            const s = (l.source && typeof l.source === 'object') ? l.source.id : l.source;
            const t = (l.target && typeof l.target === 'object') ? l.target.id : l.target;
            const key = `${s}---${t}`;
            pairCount[key] = (pairCount[key] || 0) + 1;
        });

        const links = graphData.links.map(l => {
            let s = l.source;
            let t = l.target;
            //  source/target 
            if (typeof s === 'string' && /^\d+$/.test(s)) s = Number(s);
            if (typeof t === 'string' && /^\d+$/.test(t)) t = Number(t);

            //  source/target  label nodes  id
            if (!idSet.has(s)) {
                const found = nodes.find(n => n.label === s || String(n.label) === String(s));
                if (found) s = found.id;
            }
            if (!idSet.has(t)) {
                const found = nodes.find(n => n.label === t || String(n.label) === String(t));
                if (found) t = found.id;
            }

            // 
            const revKey = `${t}---${s}`;
            const hasReverse = !!pairCount[revKey];
            // 
            let arrowRelPos = 0.9;
            if (hasReverse) {
                //  id  link 
                arrowRelPos = (String(s) < String(t)) ? 0.85 : 0.15;
            }

            return { ...l, source: s, target: t, __hasReverse: hasReverse, __arrowRelPos: arrowRelPos };
        });

        return { nodes, links };
    }, [graphData]);

    // 
    const degreeMap = useMemo(() => {
        const map = {};
        normalizedGraphData.links.forEach(link => {
            map[link.source] = (map[link.source] || 0) + 1;
            map[link.target] = (map[link.target] || 0) + 1;
        });
        return map;
    }, [normalizedGraphData]);

    const fgRef = useRef();

    // 
    const limitedGraphData = useMemo(() => {
        if (!normalizedGraphData) return { nodes: [], links: [] };
        if (showAllNodes) return normalizedGraphData;

        const limitedNodes = normalizedGraphData.nodes.slice(0, nodeLimit);
        const nodeIds = new Set(limitedNodes.map(n => n.id));
        const limitedLinks = normalizedGraphData.links.filter(link => 
            nodeIds.has(link.source) && nodeIds.has(link.target)
        );

        return {
            nodes: limitedNodes,
            links: limitedLinks
        };
    }, [normalizedGraphData, nodeLimit, showAllNodes]);

    // / helper 

    const handleNodeClick = (node) => {
        setSelectedNode(node);
        setCenterNode(node);
        if (onNodeClick) {
            //  neo4j  id 
            const arg = (node && node.neo4jId !== undefined && node.neo4jId !== null) ? node.neo4jId : node.label;
            onNodeClick(arg);
        }
    };

    //  nodeColors  legendItems

    //  centerNode 
    useEffect(() => {
        if (!centerNode || !fgRef.current) return;
        // 
        setTimeout(() => {
            try {
                if (centerNode.x !== undefined && centerNode.y !== undefined) {
                    fgRef.current.centerAt(centerNode.x, centerNode.y, 400);
                    fgRef.current.zoom(1.4);
                } else {
                    //  fit
                    fgRef.current.zoomToFit(400);
                }
            } catch (e) {
                // 
            }
        }, 150);
    }, [centerNode]);

    //  d3 
    useEffect(() => {
        if (!fgRef.current) return;
        try {
            const fg = fgRef.current;
            // 
            fg.d3Force('charge', forceManyBody().strength(-80));
            fg.d3Force('link', forceLink().distance(80).id(d => d.id));
            fg.d3Force('collide', forceCollide().strength(0.9).radius(d => {
                //  degree 
                const deg = degreeMap[d.id] || 0;
                const base = useDegreeScaling ? (6 + deg * 2) : 10;
                return Math.max(10, base + 6);
            }));
            fg.d3Force('center', forceCenter());
            // 
            fg.d3ReheatSimulation();
        } catch (e) {
            //  d3 
        }
    }, [normalizedGraphData, degreeMap, useDegreeScaling]);

    if (!graphData) {
        return <p>...</p>;
    }

    return (
        <div className="graph-container">
            <div className="graph-controls">
                <div className="node-count">
                    : {limitedGraphData.nodes.length} / {graphData.nodes.length}
                </div>
                <div className="node-limit-control">
                    <input
                        type="range"
                        min="10"
                        max={graphData.nodes.length}
                        value={nodeLimit}
                        onChange={(e) => setNodeLimit(Number(e.target.value))}
                    />
                    <span>{nodeLimit} </span>
                    <button 
                        onClick={() => setShowAllNodes(!showAllNodes)}
                        className="toggle-button"
                    >
                        {showAllNodes ? '' : ''}
                    </button>
                    <label style={{marginLeft:12, display:'flex', alignItems:'center', gap:8}}>
                        <input type="checkbox" checked={useDegreeScaling} onChange={(e)=>setUseDegreeScaling(e.target.checked)} />
                        <span></span>
                    </label>
                    <label style={{marginLeft:12, display:'flex', alignItems:'center', gap:8}}>
                        <span></span>
                        <select value={theme} onChange={(e)=>setTheme(e.target.value)}>
                            <option value="neo4j">Neo4j</option>
                            <option value="vibrant">Vibrant</option>
                            <option value="muted">Muted</option>
                        </select>
                    </label>
                </div>
            </div>
            <ForceGraph2D
                ref={fgRef}
                graphData={limitedGraphData}
                nodeLabel="label"
                linkLabel="type"
                nodeColor={node => nodeColors[node.type] || nodeColors['unknown']}
                nodeRelSize={8}
                width={Math.min(1100, window.innerWidth - 380)}
                height={Math.min(800, window.innerHeight - 180)}
                onNodeClick={handleNodeClick}
                onNodeHover={node => setHoveredNode(node)}
                warmupTicks={10}
                cooldownTicks={30}
                // 
                onEngineTick={() => {}}
                d3AlphaDecay={0.05}
                controlType={'orbit'}
                nodeCanvasObject={(node, ctx, globalScale) => {
                    //  degree scaling 
                    const baseSize = 6;
                    const size = useDegreeScaling ? (baseSize + (degreeMap[node.id] || 0) * 2) : baseSize;

                    // 
                    ctx.beginPath();
                    ctx.arc(node.x, node.y, size, 0, 2 * Math.PI);
                    ctx.fillStyle = nodeColors[node.type] || nodeColors['unknown'];
                    ctx.fill();

                    //  hover
                    if (selectedNode && selectedNode.id === node.id) {
                        ctx.lineWidth = 2;
                        ctx.strokeStyle = '#333';
                        ctx.stroke();
                    }
                    // 
                    const label = node.label || '';
                    const fontSize = Math.max(10, 12 / globalScale);
                    ctx.font = `bold ${fontSize}px Sans-Serif`;
                    ctx.textAlign = 'center';
                    ctx.textBaseline = 'middle';

                    // 
                    const bg = (nodeColors[node.type] || nodeColors['unknown']).replace('#', '');
                    const r = parseInt(bg.slice(0,2),16);
                    const g = parseInt(bg.slice(2,4),16);
                    const b = parseInt(bg.slice(4,6),16);
                    const luminance = 0.299*r + 0.587*g + 0.114*b;
                    ctx.fillStyle = luminance > 150 ? '#111' : '#fff';

                    // 
                    const maxWidth = size * 2.2; // 
                    let displayText = label;
                    while (ctx.measureText(displayText).width > maxWidth && displayText.length > 0) {
                        displayText = displayText.slice(0, -1);
                    }
                    if (displayText !== label) displayText = displayText.slice(0, -2) + '';

                    ctx.fillText(displayText, node.x, node.y);
                }}
                // 
                linkCanvasObject={(link, ctx, globalScale) => {
                    if (!link.source || !link.target) return;
                    const source = (typeof link.source === 'object') ? link.source : limitedGraphData.nodes.find(n => n.id === link.source);
                    const target = (typeof link.target === 'object') ? link.target : limitedGraphData.nodes.find(n => n.id === link.target);
                    if (!source || !target || source.x == null || target.x == null) return;

                    const midX = (source.x + target.x) / 2;
                    const midY = (source.y + target.y) / 2;
                    const text = link.type || '';
                    const fontSize = Math.max(9, 10 / globalScale);
                    ctx.font = `${fontSize}px Sans-Serif`;
                    ctx.fillStyle = 'rgba(0,0,0,0.6)';
                    ctx.textAlign = 'center';
                    ctx.textBaseline = 'middle';
                    // 
                    const padding = 4;
                    const textWidth = ctx.measureText(text).width;
                    ctx.fillStyle = 'rgba(255,255,255,0.85)';
                    ctx.fillRect(midX - textWidth/2 - padding, midY - fontSize/2 - 2, textWidth + padding*2, fontSize + 4);
                    ctx.fillStyle = 'rgba(0,0,0,0.85)';
                    ctx.fillText(text, midX, midY);
                }}
                linkDirectionalArrowLength={link => (link.__hasReverse ? 6 : 8)}
                linkDirectionalArrowRelPos={link => (link.__arrowRelPos !== undefined ? link.__arrowRelPos : 0.9)}
                linkDirectionalParticles={0}
                linkWidth={link => (link.__hasReverse ? 1.2 : 1.6)}
                linkColor={link => '#5a5a5a'}
            />
            <div className="node-details">
                {hoveredNode && (
                    <div className="hover-details">
                        <h3>{hoveredNode.label}</h3>
                        <div className="properties">
                            {Object.entries(hoveredNode.properties || {}).map(([key, value]) => (
                                <div key={key} className="property">
                                    <span className="property-key">{key}:</span>
                                    <span className="property-value">
                                        {typeof value === 'object' ? JSON.stringify(value) : String(value)}
                                    </span>
                                </div>
                            ))}
                        </div>
                    </div>
                )}
                {selectedNode && (
                    <div className="selected-details">
                        <h3>{selectedNode.label}</h3>
                        <div className="properties">
                            {Object.entries(selectedNode.properties || {}).map(([key, value]) => (
                                <div key={key} className="property">
                                    <span className="property-key">{key}:</span>
                                    <span className="property-value">
                                        {typeof value === 'object' ? JSON.stringify(value) : String(value)}
                                    </span>
                                </div>
                            ))}
                        </div>
                    </div>
                )}
            </div>
            <div className="legend">
                {Object.entries(nodeColors).map(([type, color]) => (
                    <div key={type} className="legend-item">
                        <span className="color-box" style={{backgroundColor: color}}></span>
                        <span className="type-label">{type}</span>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default KnowledgeGraph;