// frontend/src/App.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import SearchBar from './components/SearchBar';
import Sidebar from './components/Sidebar';
import Neo4jRenderer from './components/Neo4jRenderer';
import Home from './components/Home';
import './App.css';
import './components/styles.css';

const API_BASE_URL = process.env.NODE_ENV === 'development' 
    ? 'http://localhost:3001'  // 
    : window.location.origin;  // ngrok

function App() {
    const [graphData, setGraphData] = useState(null);
    const [description, setDescription] = useState('No description available.');
    const [metadata, setMetadata] = useState({});
    const [activePage, setActivePage] = useState('home');

    useEffect(() => {
        if (activePage === 'knowledgeGraph') {
            fetchGraphData();
        }
    }, [activePage]);

    const fetchGraphData = async (searchTerm = '') => {
        try {
            const response = await axios.get(`${API_BASE_URL}/knowledgeGraph?search=${searchTerm}`);
            setGraphData(response.data);
            setDescription(response.data.description || 'No description available.');
            setMetadata(response.data.metadata || {});
        } catch (error) {
            console.error('Error fetching data:', error);
        }
    };

    const fetchNodeCenteredGraph = async (nodeId) => {
        try {
            const response = await axios.get(`${API_BASE_URL}/knowledgeGraph/node/${nodeId}`);
            setGraphData(response.data);
            setDescription(response.data.description || 'No description available.');
            setMetadata(response.data.metadata || {});
        } catch (error) {
            console.error('Error fetching node centered data:', error);
        }
    };

    const handleSearch = (searchTerm) => {
        fetchGraphData(searchTerm);
    };

    const handleNodeClick = (arg) => {
        //  neo4j  id id 
        if (typeof arg === 'number' || (typeof arg === 'string' && /^\d+$/.test(arg))) {
            fetchNodeCenteredGraph(Number(arg));
        } else {
            // 
            fetchGraphData(arg);
        }
    };

    const handleNavigation = (page) => {
        setActivePage(page);
    };

    return (
        <div className="app-container">
            <header className="app-header">
                <div className="header-content">
                    <h1></h1>
                    <nav className="main-nav">
                        <ul>
                            <li>
                                <button
                                    type="button"
                                    className={activePage === 'home' ? 'active' : ''}
                                    onClick={() => handleNavigation('home')}
                                >
                                    
                                </button>
                            </li>
                            <li>
                                <button
                                    type="button"
                                    className={activePage === 'poems' ? 'active' : ''}
                                    onClick={() => handleNavigation('poems')}
                                >
                                    
                                </button>
                            </li>
                            <li>
                                <button
                                    type="button"
                                    className={activePage === 'ontology' ? 'active' : ''}
                                    onClick={() => handleNavigation('ontology')}
                                >
                                    
                                </button>
                            </li>
                            <li>
                                <button
                                    type="button"
                                    className={activePage === 'knowledgeGraph' ? 'active' : ''}
                                    onClick={() => handleNavigation('knowledgeGraph')}
                                >
                                    
                                </button>
                            </li>
                        </ul>
                    </nav>
                </div>
            </header>
            <div className="main-content">
                {activePage === 'home' && <Home />}
                {activePage === 'knowledgeGraph' && (
                    <>
                        <Neo4jRenderer 
                            graphData={graphData} 
                            onNodeClick={handleNodeClick}
                        />
                        <Sidebar description={description} metadata={metadata} />
                        <SearchBar onSearch={handleSearch} />
                    </>
                )}
                {activePage === 'poems' && (
                    <div className="page-content">
                        <h2></h2>
                        <p>...</p>
                    </div>
                )}
                {activePage === 'ontology' && (
                    <div className="page-content">
                        <h2></h2>
                        <p>...</p>
                    </div>
                )}
            </div>
            <footer className="app-footer">
                <p>Copyright  </p>
            </footer>
        </div>
    );
}

export default App;